.product__rating{
    display:flex;

}

.product__info{
    height: 100px;
    margin-bottom:15px;
}

.product__price{
    margin-top:5px;
}

.product{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
      /* max-width: 100px;
     max-height: 400px;  */
    margin:10px;
     padding: 20px; 
     width: 100%;
    background-color: white;
    border-color: #a88734;
    z-index:1;

}


.product > img{
   max-height: 200px;
   width: 100%;
   
   object-fit:contain;
   margin-bottom:15px;
}

.product > button{

      background-color:orange ;
      border:1px solid;
      border-color:#a88734 #9c7e31 #846a29;
}